.card {
  background-color: #fff;
  border-radius: 8px;
  flex-direction: column;
  align-items: center;
  gap: 20px;
  width: 200px;
  height: 350px;
  padding: 18px;
  display: flex;
  box-shadow: 0 4px 12px #00000026;
}

.card__computer {
  background-image: url("imac.202c52ff.jpeg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  width: 160px;
  height: 134px;
}

.card__content {
  flex-direction: column;
  gap: 5px;
  width: 100%;
  display: flex;
}

.card__title {
  color: #000;
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 13px;
  font-weight: 500;
}

.card__code {
  color: #616070;
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 13px;
  font-weight: 400;
}

.card__stars {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  display: flex;
}

.card__stars-list {
  gap: 5px;
  display: flex;
}

.card__star {
  background-image: url("star.75a8b2fa.svg");
  background-repeat: no-repeat;
  background-size: contain;
  width: 16px;
  height: 16px;
}

.card__star--active {
  background-image: url("star-active.896e078e.svg");
}

.card__reviews {
  color: #000;
  text-align: end;
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 13px;
  font-weight: 400;
}

.card__price {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  display: flex;
}

.card__price-title {
  color: #616070;
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 13px;
  font-weight: 400;
}

.card__price-value {
  color: #000;
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 13px;
  font-weight: 700;
}

.card__button {
  color: #fff;
  cursor: pointer;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  width: 100%;
  height: 40px;
  font-family: Roboto, sans-serif;
  font-weight: 700;
}

.card__button:hover {
  color: #00acdc;
  background-color: #fff;
}
